import React from 'react'
import Header from "../../Component/Layout/Footer/Header"
import Footer from "../../Component/Layout/Footer/Footer"
import PlaneAnimation from '../../Component/PlaneAnimation'
import ArrivalHero from '../../Component/ArrivalHero'
import LuggageManagement from '../../Component/LuggageManagement'

const Arrival = () => {
  return (
    <>
      <Header arrivals />
      <ArrivalHero />
      <PlaneAnimation />
      <LuggageManagement />
      {/* <ArrivalHero2 /> */}
      <Footer />
    </>
  )
}

export default Arrival