import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import baggageClaim from "../../assets/baggageTrack.jpg";
import { Bounce, ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Modal, Box, Typography, Grid } from '@mui/material';
import dayjs from 'dayjs';
import luggage from "../../assets/luggage.png"
import baggageClaimed from "../../assets/baggageClaimed.png"
import "./index.css"
import { ArrowDropUp, ArrowUpward } from "@mui/icons-material";

const statusLevelMap = {
  0: "Entered",
  1: "Submitted",
  2: "In Transit",
  3: "Completed",
  4: "Canceled",
  5: "Canceled (Billable)",
  6: "Assigned",
  7: "Assigned (In Transit)",
  8: "Unassigned",
  9: "Quoted",
};


const BaggageTrackHero = () => {
  const [trackingNumber, setTrackingNumber] = useState("");
  const [trackingId, setTrackingId] = useState("");
  const [trackingData, setTrackingData] = useState(null);
  const [open, setOpen] = useState(false);

  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  useEffect(() => {
    if (trackingId !== "") {
      (async () => {
        try {
          const response = await axios.get(`https://secure.ontime360.com/sites/aafcouriers/api/orders/${trackingId}`, {
            headers: {
              'Accept': 'application/json',
              'Authorization': '4fe1b4ce-9dd4-4375-abb8-31bab61bd149',
            },
          });
          console.log(response.data);
          setTrackingData(response.data);
          handleOpen()
        } catch (error) {
          console.log(error);
        }
      })()
    }
  }, [trackingId])

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      console.log("started");
      const response = await axios.get('https://secure.ontime360.com/sites/aafcouriers/api/orders', {
        headers: {
          'Accept': 'application/json',
          'Authorization': '4fe1b4ce-9dd4-4375-abb8-31bab61bd149',
        },
        params: {
          trackingNumber: trackingNumber,
        },
      });
      if (response.data === null) {
        toast.error('🦄 No Record Found!', {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "light",
          transition: Bounce,
        });
        return
      } else {
        console.log(response.data);
        setTrackingId(response.data)
      }
    } catch (error) {
      console.log(error);
      toast.error('🦄 Error getting data !', {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
        transition: Bounce,
      });
    }
  };

  return (
    <>
      <ToastContainer />
      {/* <div className="relative bg-gradient-to-r from-[#FEDD5A] to-[#FDC23E] overflow-hidden">
        <div className="max-w-7xl mx-auto px-4 sm:py-20">
          <div className="relative z-10 pb-8 bg-gradient-to-r from-[#FEDD5A] to-[#FDC23E] sm:pb-16 md:pb-20 lg:max-w-2xl lg:w-full lg:pb-28 xl:pb-32">
            <svg
              className="hidden lg:block absolute right-0 inset-y-0 h-full w-48 text-[#FDC23E] transform translate-x-1/2"
              fill="currentColor"
              viewBox="0 0 100 100"
              preserveAspectRatio="none"
              aria-hidden="true"
            >
              <polygon points="50,0 100,0 50,100 0,100" />
            </svg>

            <div className="relative pt-6 px-4 sm:px-6 lg:px-8">
              <nav
                className="relative flex items-center justify-between sm:h-10 lg:justify-start"
                aria-label="Global"
              >
                <div className="flex items-center flex-grow flex-shrink-0 lg:flex-grow-0">
                  <div className="flex items-center justify-between w-full md:w-auto">
                    <Link to="/">
                      <span className="sr-only">BaggageClaimed</span>
                      <img
                        className="h-8 w-auto sm:h-10 rounded-lg"
                        src={baggageClaim}
                        alt="BaggageClaimed"
                      />
                    </Link>
                  </div>
                </div>
              </nav>
            </div>

            <div className="mt-6 sm:max-w-lg sm:mx-auto sm:text-center lg:text-left lg:max-w-2xl">
              <h1 className="text-4xl tracking-tight font-extrabold text-white sm:text-5xl md:text-6xl">
                <span className="block xl:inline">Track Your Baggage</span>
              </h1>
              <p className="mt-4 text-xl text-white">
                Check the status of your luggage with ease. Enter your tracking information below to get started.
              </p>
              <div className="mt-6">
                <form onSubmit={handleSubmit} className="flex justify-center">
                  <div className="max-w-lg w-full lg:max-w-xl">
                    <label htmlFor="tracking-number" className="sr-only">
                      Tracking Number
                    </label>
                    <div className="relative">
                      <input
                        type="text"
                        id="tracking-number"
                        name="tracking-number"
                        value={trackingNumber}
                        onChange={(e) => setTrackingNumber(e.target.value)}
                        className="block w-full px-3 py-3 border-2 border-[#FDC23E] rounded-md placeholder-gray-400 shadow-sm focus:outline-none focus:border-[#FEDD5A] focus:ring-1 focus:ring-[#FEDD5A] focus:ring-opacity-50 sm:text-lg"
                        placeholder="Enter your tracking number"
                        required
                      />
                      <button
                        type="submit"
                        className="absolute inset-y-0 right-0 px-4 py-3 bg-[#FDC23E] text-white rounded-r-md shadow-sm hover:bg-[#FEDD5A] focus:outline-none focus:ring-1 focus:ring-[#FEDD5A] focus:ring-opacity-50"
                      >
                        Track
                      </button>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
        <div className="lg:absolute lg:inset-y-0 lg:right-0 lg:w-1/2">
          <img
            className="h-56 w-full object-cover sm:h-72 md:h-96 lg:w-full lg:h-full"
            src={baggageClaim}
            alt="Baggage Track"
          />
        </div>
      </div> */}

      <div className="flex justify-center items-center h-screen w-full bg-[#FFDA31]">
        <div className="w-[85vw] grid grid-cols-2 gap-10 items-center pt-16">
          <div>
            <h1 className="text-6xl font-bold text-center">Track Your Baggage</h1>
            <p className="mt-4 w-80 text-lg mx-auto text-center">Check the status of you luggage by entering the tracking number below</p>
            <form onSubmit={handleSubmit} className="flex justify-center items-center gap-3 mt-6">
              <input
                type="text"
                id="tracking-number"
                name="tracking-number"
                value={trackingNumber}
                onChange={(e) => setTrackingNumber(e.target.value)}
                className="w-64 px-3 py-3 border-2 border-[#FDC23E] rounded-md placeholder-gray-400 shadow-sm focus:outline-none focus:border-[#FEDD5A] focus:ring-1 focus:ring-[#FEDD5A] focus:ring-opacity-50 sm:text-lg"
                placeholder="Enter your tracking number"
                required
              />
              <button
                type="submit"
                className="bg-[#EB2131] w-16 h-16 rounded-full flex justify-center items-center"
              >
                <ArrowUpward className="text-[#FFDE59]" fontSize="large" />
              </button>
            </form>
          </div>

          <div className="w-full">
            <img src={luggage} className="!w-full" />
          </div>
        </div>
      </div>

      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-title"
        aria-describedby="modal-description"
        sx={{ display: "flex", justifyContent: "center", alignItems: "center" }}
      >
        <Box
          sx={{
            maxWidth: 600,
            px: 4,
            pb: 2,
            pt: 4,
            bgcolor: 'background.paper',
            borderRadius: 2,
            boxShadow: 24,
            textAlign: 'left',
            position: "relative",
            overflow: "hidden"
          }}
        >
          <div onClick={handleClose} className="!absolute !right-0 !top-0 -mt-10 -mr-8">
            <span className="close-button !scale-[.255]">Close</span>
          </div>
          {/* <Divider sx={{ mb: 2 }} /> */}
          <div className="flex justify-center items-center -mt-16">
            <img src={baggageClaimed} alt="baggageClaimed" className="hanging-image" />
          </div>
          <Grid container spacing={2} /* sx={{ mt: .2, mb: 2 }} */ className="!mt-4 !mb-6">
            <Grid item xs={6}>
              <Typography><strong>Tracking number:</strong></Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography>{trackingData?.TrackingNumber}</Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography><strong>Order received:</strong></Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography>{dayjs(trackingData?.DateSubmitted).format('MM/DD/YYYY h:mm A')} (Eastern Daylight Time)</Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography><strong>Origin:</strong></Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography>{`${trackingData?.CollectionLocation.City}, ${trackingData?.CollectionLocation.State}, ${trackingData?.CollectionLocation.Country}`}</Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography><strong>Status:</strong></Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography>{statusLevelMap[trackingData?.StatusLevel]}</Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography><strong>Destination:</strong></Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography>{`${trackingData?.DeliveryLocation.City}, ${trackingData?.DeliveryLocation.State}, ${trackingData?.DeliveryLocation.Country}`}</Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography><strong>Collected from:</strong></Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography>{trackingData?.CollectionContactName}</Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography><strong>Actual collection time:</strong></Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography>{dayjs(trackingData?.CollectionArrivalDate).format('MM/DD/YYYY h:mm A')}</Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography><strong>Actual delivery time:</strong></Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography>{dayjs(trackingData?.DeliveryArrivalDate).format('MM/DD/YYYY h:mm A')}</Typography>
            </Grid>
          </Grid>
          {/* <Divider sx={{ mb: 2 }} /> */}
        </Box>
      </Modal>
    </>
  );
};

export default BaggageTrackHero;
