import React from "react";
import { Route, Routes } from "react-router-dom";
import { Arrival, BaggageTrack, Booking, Contact, CookiePolicy, Departures, Home, Login, Lost, Privacy, Signup, TermsConditions, WebsiteAcceptable, WebsiteTerms } from "./Pages";
import 'react-toastify/dist/ReactToastify.css';
import { ToastContainer } from "react-toastify";

const App = () => {
  return (
    <div className="!font-sans">
      <ToastContainer />
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/booking" element={<Booking />} />
        <Route path="/departures" element={<Departures />} />
        <Route path="/arrival" element={<Arrival />} />
        <Route path="/contact" element={<Contact />} />
        <Route path="/lost" element={<Lost />} />
        <Route path="/baggage" element={<BaggageTrack />} />
        <Route path="/cookie-policy" element={<CookiePolicy />} />
        <Route path="/terms" element={<TermsConditions />} />
        <Route path="/website-acceptable-use" element={<WebsiteAcceptable />} />
        <Route path="/website-terms" element={<WebsiteTerms />} />
        <Route path="/privacy" element={<Privacy />} />
        <Route path="/signup" element={<Signup />} />
        <Route path="/login" element={<Login />} />
      </Routes>
    </div>
  );
};

export default App;
