import React, { useState } from 'react'
import {
    TextField,
} from "@mui/material";

const Step1 = ({ setShowUser, setZipCode, fetchLocations }) => {
    const handleSubmit = e => {
        e.preventDefault()
        fetchLocations()
        setShowUser(true)
    }
    return (
        <>
            <h1 className="text-4xl !font-extrabold text-[#1F1F1F]">
                Choose your delivery address
            </h1>
            <form onSubmit={handleSubmit}>
                <TextField
                    onChange={(e) => setZipCode(e.target.value)}
                    label="Enter your postcode"
                    className="!mt-7 w-full !rounded-2xl"
                />
                <button
                    className="bg-[#FFDE59] w-full py-5 rounded-3xl text-2xl font-semibold text-[#1F1F1F] hover:bg-opacity-85 transition-opacity duration-500 mt-7"
                    type='submit'
                >
                    Search for my address
                </button>
            </form>
        </>
    )
}

export default Step1
