import React from 'react'
import logo from "../../assets/logo1.png";
import porter from "../../assets/porters.jpg";
import google from "../../assets/google.png";
import apple from "../../assets/apple.png";
import { Link } from 'react-router-dom';

const First = ({ setFirst, setStep1 }) => {
    return (
        <>
            <div className='col-span-5 pt-1'>
                <img src={logo} alt="logo" className="ml-[5vw] h-16 drop-shadow-sm z-50 mb-24" />
                <div className='flex items-center flex-col'>
                    <h1 className='text-center text-3xl font-bold w-[65%] text-[#453536] mb-10'>Ready to breeze through your travels? Join us now!</h1>
                    <button className='w-[67%] text-white bg-[#EB2131] rounded-2xl py-4 font-semibold text-lg mb-4' onClick={() => { setFirst(false); setStep1(true) }}>Signup with email</button>
                    <button className='w-[67%] rounded-2xl py-4 justify-center font-semibold text-lg mb-4 flex gap-2 items-center border-black border-[1px]'> <img src={google} alt="google" className='h-5' /> Sign up with Google</button>
                    <button className='w-[67%] rounded-2xl py-4 justify-center font-semibold text-lg mb-8 flex gap-2 items-center border-black border-[1px]'> <img src={apple} alt="google" className='h-5' /> Sign up with Apple</button>

                    <div className='text-center'>
                        <p className='text-gray-500'>Already have an account?</p>
                        <Link to="/login" className='text-[#EB2131]'>Log in instead</Link>
                    </div>
                </div>
            </div>
            <div className='col-span-5'>
                <img src={porter} className='h-screen !w-full object-cover rounded-lg' />
            </div>
        </>
    )
}

export default First