import React from "react";

import transfer from "../../assets/stickySidebar/main/transfer.png";
import lost from "../../assets/stickySidebar/main/lost.png";
import storageIcon from "../../assets/stickySidebar/main/storage.png";

import luggageStorage from "../../assets/stickySidebar/luggageStorage.mp4";
import baggageTransfer from "../../assets/stickySidebar/baggageTransfer.mp4";
import lostLuggage from "../../assets/stickySidebar/lostLuggage.mp4";

import pickup from "../../assets/stickySidebar/transfer/pickup.png";
import drop from "../../assets/stickySidebar/transfer/drop.png";
import realTime from "../../assets/stickySidebar/transfer/realTime.png";
import secure from "../../assets/stickySidebar/transfer/secure.png";

import delivery from "../../assets/stickySidebar/lost/delivery.png";
import partnership from "../../assets/stickySidebar/lost/partnership.png";
import response from "../../assets/stickySidebar/lost/response.png";
import hassle from "../../assets/stickySidebar/lost/hassle.png";

import affordable from "../../assets/stickySidebar/storage/affordable.png";
import location from "../../assets/stickySidebar/storage/location.png";
import flexible from "../../assets/stickySidebar/storage/flexible.png";
import secureFacilities from "../../assets/stickySidebar/storage/secure.png";

import "./index.css";
import FadeInSection from "./FadeInSection";

const servicesData = [
  {
    sidebar: {
      icon: transfer,
      title: "Luggage Transfer",
      description:
        "The easiest way to handle your luggage needs during travel.",
    },
    main: {
      img: baggageTransfer,
      items: [
        {
          icon: pickup,
          title: "Airport Pickup",
          description:
            "We pick up your luggage from baggage claim and deliver it to your hotel or residence efficiently and securely",
        },
        {
          icon: drop,
          title: "Airport Drop-off",
          description:
            "We collect your luggage before departure and ensure timely delivery to the airport, allowing you to travel worry-free.",
        },
        {
          icon: secure,
          title: "Secure Handling",
          description:
            "Your luggage is handled with utmost care, ensuring it arrives safely and promptly at your destination",
        },
        {
          icon: realTime,
          title: "Real-Time Updates",
          description:
            "Receive text notifications and track your luggage's status anytime on our website for complete transparency.",
        },
      ],
    },
  },
  {
    sidebar: {
      icon: lost,
      title: "Lost Luggage Retrieval",
      description:
        "Effortlessly reclaim your misplaced luggage without the hassle.",
    },
    main: {
      img: lostLuggage,
      items: [
        {
          icon: response,
          title: "Fast Response",
          description:
            "We quickly initiate the retrieval process, working with major airlines to locate your lost luggage efficiently.",
        },
        {
          icon: partnership,
          title: "Airline Partnerships",
          description:
            "We collaborate directly with major airlines to retrieve your luggage from the airport promptly.",
        },
        {
          icon: hassle,
          title: "Hassle-Free Service",
          description:
            "Avoid the inconvenience of returning to the airport; we handle everything and bring your luggage to you.",
        },
        {
          icon: delivery,
          title: "Direct Delivery",
          description:
            "We retrieve your bags from the airport and deliver them directly to your final destination, saving you time and hassle.",
        },
      ],
    },
  },
  {
    sidebar: {
      icon: storageIcon,
      title: "Luggage Storage",
      description:
        "Enjoy your layover to the fullest without the burden of luggage.",
    },
    main: {
      img: luggageStorage,
      items: [
        {
          icon: secureFacilities,
          title: "Secure Facilities",
          description:
            "Our storage locations feature 24/7 monitoring, biometric access, and surveillance cameras, ensuring your luggage's safety and security at all times.",
        },
        {
          icon: location,
          title: "Convenient Locations",
          description:
            "Strategically placed near major airports, our storage centers provide easy drop-off and pick-up to fit your travel schedule.",
        },
        {
          icon: flexible,
          title: "Flexible Storage Options",
          description:
            "We offer short-term and long-term storage solutions, customizable to meet your needs, from a few hours to several months.",
        },
        {
          icon: affordable,
          title: "Affordable Pricing",
          description:
            "Competitive rates with a transparent pricing structure and discounts for extended storage periods, ensuring cost-effective and budget-friendly services",
        },
      ],
    },
  },
];

const StickySidebar = () => {
  return (
    <>
      {servicesData.map((service, index) => (
        <div key={index} className="w-full flex justify-center mt-28">
          <div className="grid grid-cols-2 gap-10 lg:w-[80vw] w-[92vw] 2xl:w-[1500px]">
            <aside>
              <div className="sidebar md:h-[25vh] md:mb-0 mb-20 md:bg-transparent md:min-h-[500px] overflow-auto sticky md:top-[16%] top-[1%] md:py-0 py-5 md:!w-[546.5px]">
                <img
                  src={service.sidebar.icon}
                  alt={service.sidebar.title}
                  className="w-20 -rotate-12"
                />
                <h1 className="md:mt-8 text-5xl font-bold max-w-[10em]">
                  {service.sidebar.title}
                </h1>
                <p className="text-[#909399] text-2xl mt-6 max-w-[17em]">
                  {service.sidebar.description}
                </p>
              </div>
            </aside>
            <div className="w-full flex flex-col">
              <div className="max-w-[522px]">
                <video
                  src={service.main.img}
                  className="w-full object-cover rounded-2xl"
                  alt={service.sidebar.title}
                  autoPlay
                  loop
                  muted
                />
                {service.main.items.map((item, idx) => (
                  <FadeInSection key={idx}>
                    <div className="mt-20 w-full flex lg:gap-55 gap-4">
                      <img
                        className="w-[56px] h-[56px] -mt-2 object-contain"
                        src={item.icon}
                        alt={item.title}
                      />
                      <div className="w-full">
                        <h1 className="text-[28px] font-semibold">
                          {item.title}
                        </h1>
                        <p className="text-[#50555c] mt-4 text-xl">
                          {item.description}
                        </p>
                      </div>
                    </div>
                  </FadeInSection>
                ))}
              </div>
            </div>
          </div>
        </div>
      ))}
    </>
  );
};

export default StickySidebar;
