import React from "react";
import plane2 from "../../assets/plane2.png";
import runway from "../../assets/runway.jpeg";
import cloud1 from "../../assets/cloud1.jpg";
import Marquee from "react-fast-marquee";

const PlaneAnimation = () => {

  return (
    <>
      <section className="flex justify-start pt-10">
        <div className="relative">
          <div className="w-full">
            <img
              src={cloud1}
              className="w-full min-h-[70vh] object-cover"
              alt="cloud"
            />
          </div>
          <div className="absolute top-10 w-full">
            <Marquee speed={400} direction="right" className="w-full overflow-hidden">
                <img
                  className="mt-44 rotate-2 duration-[5000ms] max-h-[320px] !overflow-hidden ml-[100vw]"
                  src={plane2}
                  alt="plane"
                />
            </Marquee>
          </div>
          <div className="w-full -mt-16">
            <img src={runway} className="w-full" />
          </div>
          <div className="absolute top-0 z-40 left-0 w-full pt-28 text-white">
            <div className="flex justify-center">
              <h1 className="text-5xl text-center font-semibold max-w-[500px]">
                Experience Effortless Luggage Handling
              </h1>
            </div>
            <div className="flex justify-center mt-5">
              <p className="text-center max-w-[800px] text-xl">
                Discover the ease of travel with BaggageClaimed, LLC. Our VIP
                luggage services ensure your bags are seamlessly transferred
                from the airport to your destination, giving you more time to
                enjoy your journey.
              </p>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default PlaneAnimation;
