import React from 'react';
import step1Icon from '../../assets/report.png';
import step2Icon from '../../assets/locate.png';
import step3Icon from '../../assets/reclaim.png';

const HowItWorks = () => {
  return (
    <div className="bg-gray-100 py-20">
      <div className="container mx-auto px-4 text-center">
        <h2 className="text-4xl font-bold mb-12 text-gray-800">How It Works</h2>
        <div className="flex flex-wrap justify-center">
          <div className="w-full md:w-1/3 px-4 mb-8">
            <div className="bg-white p-6 rounded-lg shadow-lg h-full">
              <img src={step1Icon} alt="Step 1" className="h-24 mx-auto mb-4" />
              <h3 className="text-2xl font-semibold mb-2 text-gray-700">Step 1: Report</h3>
              <p className="text-gray-600">
                File a claim through our online form or at the nearest airport information desk. Provide details of the lost item.
              </p>
            </div>
          </div>
          <div className="w-full md:w-1/3 px-4 mb-8">
            <div className="bg-white p-6 rounded-lg shadow-lg h-full">
              <img src={step2Icon} alt="Step 2" className="h-24 mx-auto mb-4" />
              <h3 className="text-2xl font-semibold mb-2 text-gray-700">Step 2: Locate</h3>
              <p className="text-gray-600">
                Our team will search our lost and found inventory and coordinate with airport staff to locate your item.
              </p>
            </div>
          </div>
          <div className="w-full md:w-1/3 px-4 mb-8">
            <div className="bg-white p-6 rounded-lg shadow-lg h-full">
              <img src={step3Icon} alt="Step 3" className="h-24 mx-auto mb-4" />
              <h3 className="text-2xl font-semibold mb-2 text-gray-700">Step 3: Reclaim</h3>
              <p className="text-gray-600">
                Once found, we will notify you and arrange for the return of your item. You can choose to collect it or have it delivered.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default HowItWorks;
